.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.firstQuestion{
  display: flex;
            justify-content: center; /* Center horizontally */
            align-items: center;     /* Center vertically */    
}

.container {
  display: flex;
  justify-content: center; /* Center horizontally */
  align-items: center;     /* Center vertically */
  text-align: center;      /* Center text inside container */
}

.text-center {
  text-align: center;
}

.form {
  margin-top: 20px;
}

.input {
  padding: 8px;
  font-size: 16px;
}

.button {
  padding: 8px 16px;
  font-size: 16px;
  margin-left: 10px;
  cursor: pointer;
}



.animation-wait {
  animation: waitAnimation 3s;
}


@keyframes waitAnimation {
  0% {
      opacity: 0;
  }

  90% {
    opacity: 0;
  }

  100% {
      opacity: 1;
  }
}